body{
  position: relative;
  margin: 0;
  padding: 0;
  font-size: 16px;
  color: #333;
  font-family: 'Lato', 'Noto Sans JP', '游ゴシック Medium', '游ゴシック体', 'Yu Gothic Medium', YuGothic, 'ヒラギノ角ゴ ProN', 'Hiragino Kaku Gothic ProN', 'メイリオ', Meiryo, 'ＭＳ Ｐゴシック', 'MS PGothic', sans-serif;
}
/*--------------header----------*/
.header{
  height: 80px;
  width: 100%;
  display: flex;
  align-items: center;
  &-logo{
    margin-left: 30px;
    p{
      font-size: 28px;
      font-weight: 700;
      font-family: 'HelveticaNeue',sans-serif;
      color: #029ae2;
      span{
        display: inline-block;
        color: #30d0ed;
        margin-left: 10px;
      }
    }
  }
  &-nav{
    display: flex;
    margin-right: 0;
    margin-left: auto;
    &__items{
      display: flex;
      align-items: center;
    }
    &__list{
      list-style: none;
      margin-right: 51px;
      &:last-child{
        margin-right: 0;
      }
      a{
        text-decoration: none;
        font-family: 'ヒラギノ角ゴ Pro', sans-serif;
        font-size: 14px;
        color: #333;
      }
    } 
    &__contact{
      display: block;
      padding: 30px 50px;
      margin-left: 51px;
      text-decoration: none;
      background-color: #30d0ed;
      color: #ffffff;
      font-family: 'ヒラギノ角ゴ Pro', sans-serif;
      font-size: 14px;
    }
  }
}
/*----------------main---------------*/
.main{
  position: relative;
  background-image: url("../../dist/img/mainbg.jpg");
  background-size: cover;
  width: auto;
  height: 670px;
  text-align: center;
  &-txt{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50% , -50%);
    h1{
      font-size: 140px;
      font-family: "Cormorant Garamond", serif;
      font-weight: 400;
      color: #fff;
      line-height: 115px;
      margin-bottom: 44px;
    }
    p{
      font-family: "ヒラギノ角ゴ ProN W6", serif;
      color: #fff;
      font-size: 25px;
    }
  }  
}
/*-------------News-------------*/
.news{
  height: 235px;
  background-color: #edfcff;
  &-container{
    display: flex;
    width: 1200px;
    margin: 0 auto;
    padding-top: 45px;
  }
  &__ttlMenu{
    margin-right: 45px;
    h1{
      font-size: 40px;
      color: #029ae2;
      font-family: "Open Sans" ,sans-serif;
      font-weight: 400;
    }
    a{
      display: block;
      position: relative;
      text-decoration: none;
      font-family: "ヒラギノ角ゴ Pro", sans-serif;
      font-size: 12px;
      color: #333;
      &::after{
        content: '';
        width: 6px;
        height: 6px;
        border: 0;
        border-top: 2px solid #333;
        border-right: 2px solid #333;
        transform: rotate(45deg);
        position: absolute;
        top: 50%;
        margin-top: -4px;
        margin-left: 1rem;
      }
    }
  }
  &-content{
    &__list{
      display: flex;
      position: relative;
      padding: 10px 0;
      border-bottom: 1px solid #30d0ed;
      a{
        position: absolute;
        width: 100%;
        height: 100%;
        font-size: 13px;
        font-family: "ヒラギノ角ゴ Pro", sans-serif;
        letter-spacing: 0.075rem;
        color: #333;
      }
      p{
        font-size: 13px;
        margin-right: 30px;
        letter-spacing: 0.075rem;
      }
      &__tag{
        width: 80px;
        height: 22px;
        text-align: center;
        background-color: #029ae2;
        color: #fff;
        font-weight: 700;

      }
    }
  }
}
/*-----------service-------------*/
.service{
  text-align: center;
  width: 1200px;
  margin: 0 auto;
  padding: 80px 0;
  &-ttlMenu{
    h1{
      font-size: 40px;
      font-family: "Open Sans" ,sans-serif;
      font-weight: 400;
      color: #029ae2;
    }
    h2{
      font-size: 14px;
      font-weight: 700;
      color: #029ae2;
      font-family: Helvetica;
    }
  }
  &-images{
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    img{
      width: 380px;
      height: 220px;
    }
  }
}
/*------------footer-----------*/
.footer{
  background-color: #edfcff;
  padding: 10px 0;
  text-align: center;
  small{
    font-size: 14px;
    color: #4d4d4d;
  }
}